.card {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  padding-bottom: 8px;
}

.cardContent {
  display: flex;
  flex-direction: column;
  padding: 8px;
  flex-grow: 1;
}

.cardImage {
  height: 300px;
  object-fit: cover;
  max-width: 100%;
}

.cardName {
  margin-top: 16px;
  margin-bottom: 0;
  font-size: 24px;
  font-weight: 500;
}

.cardDescr {
  line-height: 1.5;
  flex-grow: 1;
}

.cardPrice {
  display: flex;
  align-items: center;
  margin-top: 24px;
  font-size: 20px;
}

.cardPrice i {
  margin-right: 8px;
  font-size: 24px;
}

.cardReviews {
  display: block;
  margin-top: 16px;
}

.cardButton {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 16px;
  width: calc(100% - 16px);
}

.tagList {
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  margin: 0;
  list-style: none;
}

.tagListItem {
  padding: 4px 8px;
  margin: 4px;
  border-radius: 2px;
  background-color: #3f51b5;
  color: #fff;
}

.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  background-color: #2196f3;
  border: 0;
  outline: 0;
  padding: 8px 24px;
  border-radius: 4px;
  cursor: pointer;
  box-shadow: 0px 2px 1px 1px rgba(0, 0, 0, 0.05);
  font: inherit;
  font-weight: 500;
  user-select: none;
  transition: background-color 200ms ease-in-out;
}

.button:hover {
  box-shadow: 0px 2px 1px 1px rgba(0, 0, 0, 0.15);
  background-color: #1976d2;
}

.buttonIcon {
  margin-right: 8px;
}
